import "./cart-dropdown.styles.scss"
import CartItemImage from '../../assets/images/50.png'
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import { faTimes } from "@fortawesome/free-solid-svg-icons"
import { connect } from 'react-redux'
import { toggleCartHidden } from "../../redux/cart/cart.actions"
import { CustomButton } from "../custom-button/custom-button.component"
import CartItem from "../cart-item/cart-item.component"
import { selectCartItems } from "../../redux/cart/cart.selectors"
import { createStructuredSelector } from 'reselect'
import { withRouter } from "react-router-dom"

const CartDropdown = ({ cartItems, history, dispatch }) => (
    <div className="cart-dropdown">
        <span>
            <FontAwesomeIcon onClick={() => {dispatch(toggleCartHidden())}} className="cart-close" icon={faTimes} />
        </span>
        <ul className="cart-dropdown-list">

            {
                cartItems.length ?
                    cartItems.map(cartItem => (
                        <CartItem key={cartItem.id} item={cartItem} />
                    ))
                    : <p className="cart-empty-text">there is nothing in your bag!!</p>
            }

        </ul>

        <CustomButton onClick={() => {
            history.push('/checkout')
            dispatch(toggleCartHidden())
        }} inverted>GO TO CHECKOUT</CustomButton>
    </div>
)

const mapStateToProps = createStructuredSelector({
    cartItems: selectCartItems
})

export default withRouter(connect(mapStateToProps)(CartDropdown))